import { MessageEntity } from 'features/messages/messagesSlice';

// https://regex101.com/r/jGbDV1/1
const twitchClipRegex = /^(https?)?(:\/\/)?(clips\.twitch\.tv\/|(www\.)?twitch\.tv\/([\d\w]+)\/clip\/)([\d\w]+)/;

const getClipIdFromEntities = (entities: MessageEntity[]) => {
  let clipId: string | null = null;

  // eslint-disable-next-line no-restricted-syntax
  for (const entity of entities) {
    if (typeof entity === 'object' && entity.type === 'link') {
      const m = twitchClipRegex.exec(entity.text);

      if (m) {
        clipId = m[6] as string;

        return clipId;
      }
    }
  }

  return clipId;
};

export default getClipIdFromEntities;
