/* eslint-disable no-param-reassign */
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

import * as api from 'api';
import { parseClip } from 'features/chat/utils/parseApiResponse';

type ClipInfoLoading = {
  status: 'loading';
};
type ClipInfoSuccess = {
  status: 'success';
  id: string;
  url: string;
  src: string;
  srcSet: string;
  title: string;
  creatorName: string;
};
type ClipInfoError = {
  status: 'error';
};
export type ClipInfo = ClipInfoLoading | ClipInfoSuccess | ClipInfoError;

type ClipsState = Record<string, ClipInfo>;

const initialState: ClipsState = {};

type FetchClipPayload = {
  id: string;
  messageId: string;
};

export const fetchClip = createAsyncThunk(
  'clips/fetchClip',
  ({ id }: FetchClipPayload) => api.fetchClip(id),
);

const clips = createSlice({
  name: 'clips',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(fetchClip.pending, (state, { meta: { arg } }) => {
      const { id } = arg;

      state[id] = { status: 'loading' };
    });

    builder.addCase(
      fetchClip.fulfilled,
      (state, { payload, meta: { arg } }) => {
        const { id } = arg;
        const clip = parseClip(payload);

        if (!clip) {
          state[id] = { status: 'error' };

          return;
        }

        state[id] = {
          status: 'success',
          ...clip,
        };
      },
    );

    builder.addCase(fetchClip.rejected, (state, { meta: { arg } }) => {
      const { id } = arg;

      state[id] = { status: 'error' };
    });
  },
});

export default clips.reducer;
